/* Home Styles (main) */

/* 320px — 480px: Mobile devices.
481px — 768px: iPads, Tablets.
769px — 1024px: Small screens, laptops. */


*{
	margin: 0;
	padding: 0;
	list-style: none;
	text-decoration: none;
	border: none;
	outline: none;
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;

}

body {
  background-color: rgba(254,157,210,255); 
}


.Home_main__16Znb {
  display: flex;
